<template>
  <div class="structure-container">
    <div class="from">
      <Form ref="formValidate" :label-width="150">
        <FormItem label="自定义名称">
          <Input placeholder="请输入自定义的名称" v-model="bugsData.stepName" style="width: 200px"></Input>
        </FormItem>
        
          <FormItem label="jdk版本">
            <Select style="width: 200px" v-model="bugsData.peizhi">
              <Option value="jdk1.8">jdk1.8</Option>
              <Option value="jdk1.8">jdk1.7</Option>
              <Option value="jdk1.8">jdk1.6</Option>
            </Select>
          </FormItem>
          <FormItem label="maven版本">
            <Select style="width: 200px" v-model="bugsData.maven">
              <Option value="node-v8.2.1-linux-x64">node-v8.2.1-linux-x64</Option>
              <Option value="node-v8.2.1-linux-x32">node-v8.2.1-linux-x32</Option>
              
            </Select>
           
          </FormItem>
          <FormItem label="扫描目录">
            <Select style="width: 200px" v-model="bugsData.mulu">
              <Option value="1">根目录</Option>
              <Option value="2">子目录</Option>
            </Select>
          </FormItem>
          <FormItem label="findbugs命令">        
            <Input style="width: 150px" v-model="bugsData.peizhi2" placeholder="mvn findbugs:findbugs"/>
           
          </FormItem>
       
     
      </Form>
    </div>
  </div>
</template>
<script>
  import { EventBus } from '@/tools';
  export default {
    data() {
      return {
        bugsData: {
         
        }
      }
    },
    created(){
       },
    mounted(){
        EventBus.$on('add_findbugs', this.submitdata);
        EventBus.$on('echo_step_findbugs', this.echostepfindbugs);
        EventBus.$on('echo_findbugs', this.echofindBugs);
    },
    methods: {
      submitdata(){
          let _findBugsData = Object.assign({}, {stepId: 'findbugs'}, this.bugsData);
          this.$store.dispatch('add_to_stepLists', _findBugsData);
      },
      echostepfindbugs(item){ 
          this.bugsData = {};
          let _data = this.$store.getters.getSteps;
          let index = item.stepIndex;
         
          this.bugsData =  Object.assign( {},  _data[index]) ;

      },
      echofindBugs(){

      }
    }

  }
</script>
<style scoped>
  .high-level {
    width: 70%;
    margin: 20px auto;
    padding: 10px 0;
    border: 1px solid #21488a;
  }

  .form-label, .form-in-label {
    width: 150px;
    display: inline-block;
    text-align: right;
  }

  .form-in-label {
    width: 100px;
  }
</style>



